@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
	display: flex;
	flex-direction: column;
	@include m.desktop {
		flex-direction: row;
	}

	&__enlace {
        color: v.$blanco;
        font-size: 2.2rem;
        padding: 2rem;
        transition: background-color 0.3s, color 0.3s;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;

        &--actual,
        &:hover {
            background-color: v.$blanco;
            color: v.$azul;
        }
	}
}
